.app-root {
  font-family: 'Segoe UI', 'Arial', sans-serif;
  background: #f7f9fa;
  min-height: 100vh;
}
.app-header {
  background: #1976d2;
  color: #fff;
  padding: 20px 0 10px 0;
  text-align: center;
  box-shadow: 0 2px 8px #e3e3e3;
}
.app-header h1 {
  margin: 0 0 10px 0;
  font-size: 2rem;
}
.app-header nav {
  margin-bottom: 10px;
}
.app-header button {
  background: transparent;
  border: none;
  color: #fff;
  font-size: 1rem;
  margin: 0 10px;
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 4px;
  transition: background 0.2s;
}
.app-header .nav-active,
.app-header button:hover {
  background: #1565c0;
}
.app-main {
  max-width: 1200px;
  margin: 30px auto 0 auto;
  background: #fff;
  border-radius: 8px;
  min-height: 500px;
  box-shadow: 0 2px 12px #e3e3e3;
  padding: 32px 24px;
}

/* 修复输入框样式丢失问题 */
input[type="date"], input[type="text"], input[type="number"], input[type="password"], select, textarea {
  border: 1px solid #ccc;
  background: #fff;
  color: #222;
  border-radius: 4px;
  padding: 4px 8px;
  box-sizing: border-box;
}

input[type="date"]::-webkit-input-placeholder,
input[type="text"]::-webkit-input-placeholder {
  color: #aaa;
}

input[type="date"]:focus,
input[type="text"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #409eff;
  outline: none;
}

/* 保证日历图标不被遮挡 */
input[type="date"]::-webkit-calendar-picker-indicator {
  filter: invert(0.5);
} 