.modal-mask {
  position: fixed;
  z-index: 1000;
  left: 0; top: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.15);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  padding: 32px 32px 16px 32px;
  min-width: 400px;
  max-width: 90vw;
  min-height: 100px;
  position: relative;
}

.modal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 24px;
  text-align: left;
}

.modal-form label {
  display: block;
  margin-bottom: 16px;
  font-size: 15px;
  color: #333;
}

.modal-form input,
.modal-form select,
.modal-form textarea {
  width: 100%;
  margin-top: 6px;
  padding: 8px 10px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  font-size: 15px;
  box-sizing: border-box;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 18px;
}

.btn-primary {
  background: #1677ff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 7px 24px;
  font-size: 15px;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-primary:hover {
  background: #0958d9;
}

.btn-secondary {
  background: #f5f5f5;
  color: #333;
  border: none;
  border-radius: 5px;
  padding: 7px 24px;
  font-size: 15px;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-secondary:hover {
  background: #e4e4e4;
}

.form-row {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.form-label {
  width: 90px;
  color: #666;
  font-size: 15px;
  flex-shrink: 0;
}

.form-input[type="date"],
.form-input {
  flex: 1;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  padding: 8px 10px;
  font-size: 15px;
  background: #fff;
  box-sizing: border-box;
} 