.logistics-status-page {
  padding: 20px;
}
.status-toolbar {
  background-color: #f5f5f5;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 15px;
}
.status-toolbar h2 {
  margin: 0;
  font-size: 1.2em;
  margin-right: auto;
}
.status-toolbar button.active {
  background-color: #1976d2;
  color: white;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}
.status-table h3 {
  font-size: 1.1em;
  color: #333;
  margin-bottom: 10px;
}
.status-table table {
  width: 100%;
  border-collapse: collapse;
  background: #fafbfc;
}
.status-table th, .status-table td {
  border: 1px solid #e0e0e0;
  padding: 8px 6px;
  text-align: center;
}
.status-table th {
  background: #f1f5fb;
} 