.camera-page {
  display: flex;
  gap: 40px;
  align-items: flex-start;
}
.camera-form {
  flex: 0 0 320px;
  background: #f5f7fa;
  border-radius: 8px;
  padding: 24px 18px;
  box-shadow: 0 2px 8px #e3e3e3;
}
.camera-form h2 {
  margin-top: 0;
  font-size: 1.2rem;
  margin-bottom: 18px;
}
.camera-form label {
  display: block;
  margin-bottom: 12px;
  font-size: 1rem;
}
.camera-form input {
  margin-left: 10px;
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
}
.form-actions {
  margin-top: 18px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.form-actions button {
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 14px;
  cursor: pointer;
  transition: background 0.2s;
}
.form-actions button:disabled {
  background: #bdbdbd;
  cursor: not-allowed;
}
.camera-table {
  flex: 1;
  overflow-x: auto;
  max-width: 100%;
}
.camera-table h2 {
  font-size: 1.2rem;
  margin-top: 0;
  margin-bottom: 12px;
}
.camera-table table {
  width: 1400px;
  min-width: 1200px;
  border-collapse: collapse;
  background: #fafbfc;
  table-layout: fixed;
}
.camera-table th, .camera-table td {
  border: 1px solid #e0e0e0;
  padding: 8px 6px;
  text-align: center;
  white-space: nowrap;
}
.camera-table th {
  background: #f1f5fb;
}
.camera-table input[type="date"] {
  min-width: 120px;
} 