.logistics-page {
  display: flex;
  gap: 40px;
  align-items: flex-start;
}
.logistics-form {
  flex: 0 0 320px;
  background: #f5f7fa;
  border-radius: 8px;
  padding: 24px 18px;
  box-shadow: 0 2px 8px #e3e3e3;
}
.logistics-form h2 {
  margin-top: 0;
  font-size: 1.2rem;
  margin-bottom: 18px;
}
.logistics-form label {
  display: block;
  margin-bottom: 12px;
  font-size: 1rem;
}
.logistics-form input[type="text"], .logistics-form input[type="date"] {
  margin-left: 10px;
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
}
.logistics-form input[type="checkbox"] {
  margin-left: 0;
  margin-right: 6px;
}
.form-actions {
  margin-top: 18px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.form-actions button {
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 14px;
  cursor: pointer;
  transition: background 0.2s;
}
.form-actions button:disabled {
  background: #bdbdbd;
  cursor: not-allowed;
}
.logistics-table {
  flex: 1;
}
.logistics-table h2 {
  font-size: 1.2rem;
  margin-top: 0;
  margin-bottom: 12px;
}
.logistics-table table {
  width: 100%;
  border-collapse: collapse;
  background: #fafbfc;
}
.logistics-table th, .logistics-table td {
  border: 1px solid #e0e0e0;
  padding: 8px 6px;
  text-align: center;
}
.logistics-table th {
  background: #f1f5fb;
}
/* 自动换行，长内容不被截断 */
td, th {
  white-space: normal !important;
  word-break: break-all;
} 