.login-page-outer {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #e0eafc, #cfdef3 100%);
}
.login-card {
  background: #fff;
  padding: 40px 36px 32px 36px;
  border-radius: 22px;
  box-shadow: 0 8px 32px rgba(25, 118, 210, 0.10), 0 1.5px 6px rgba(0,0,0,0.04);
  min-width: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.2s;
}
.login-card h2 {
  margin-bottom: 28px;
  color: #1976d2;
  font-weight: 700;
  letter-spacing: 1px;
}
.login-page-outer .login-card form input.login-input[type="text"],
.login-page-outer .login-card form input.login-input[type="password"] {
  flex: 1 !important;
  margin-bottom: 22px !important;
  padding: 12px 16px !important;
  border-radius: 12px !important;
  border: 1.5px solid #bdbdbd !important;
  font-size: 16px !important;
  height: 44px !important;
  box-sizing: border-box !important;
  outline: none;
  transition: border 0.2s, box-shadow 0.2s !important;
  box-shadow: 0 1px 4px rgba(25, 118, 210, 0.04) !important;
}
.login-page-outer .login-card form input.login-input:focus {
  border: 1.5px solid #1976d2 !important;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.10) !important;
}
.login-btn {
  width: 100%;
  padding: 13px 0;
  background: linear-gradient(90deg, #1976d2 60%, #64b5f6 100%);
  color: #fff;
  border: none;
  border-radius: 10px;
  font-size: 1.15rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 6px;
  margin-bottom: 2px;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.08);
  transition: background 0.2s, box-shadow 0.2s;
}
.login-btn:disabled {
  background: #bdbdbd;
  cursor: not-allowed;
  box-shadow: none;
}
.login-switch-btn {
  background: none;
  border: none;
  color: #1976d2;
  font-size: 1.02rem;
  cursor: pointer;
  text-decoration: underline;
  margin-top: 0;
  padding: 0;
  border-radius: 6px;
  transition: color 0.2s, background 0.2s;
}
.login-switch-btn:hover {
  color: #125ea2;
  background: #e3f2fd;
}
.login-error {
  margin-top: 18px;
  color: #f44336;
  font-size: 1.05rem;
  text-align: center;
  font-weight: 500;
  letter-spacing: 0.5px;
}

.input-group {
  display: flex;
  gap: 0;
  width: 100%;
  justify-content: center;
}

.login-page-outer .login-card form input.login-input[type="text"],
.login-page-outer .login-card form input.login-input[type="password"] {
  border: 1.5px solid #bdbdbd !important;
}

.login-page-outer .login-card form input.login-input[type="text"] {
  border-right: none !important;
  border-radius: 12px 0 0 12px !important;
}

.login-page-outer .login-card form input.login-input[type="password"] {
  border-left: none !important;
  border-radius: 0 12px 12px 0 !important;
}

.login-card form {
  width: 100%;
}