.schedule-page {
  padding: 10px 0;
}
.schedule-toolbar {
  margin-bottom: 18px;
  display: flex;
  align-items: center;
  gap: 12px;
}
.schedule-toolbar span {
  font-weight: bold;
  font-size: 1.1rem;
}
.schedule-toolbar input[type="date"] {
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
.schedule-toolbar button { background: #2196f3; }
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 14px;
  cursor: pointer;
  transition: background 0.2s;
}
.schedule-toolbar .smart-btn { background: #4caf50; }
.schedule-toolbar button:hover {
  background: #1565c0;
}
.schedule-toolbar .smart-btn:hover {
  background: #388e3c;
}
.schedule-table {
  width: 100%;
  border-collapse: collapse;
  background: #fafbfc;
}
.schedule-table th, .schedule-table td {
  border: 1px solid #e0e0e0;
  padding: 4px 2px;
  text-align: center;
  font-size: 12px;
  font-family: 'Segoe UI', 'Arial', 'Microsoft YaHei', sans-serif;
  font-weight: 500;
  white-space: nowrap;
  vertical-align: middle;
}
.schedule-table th {
  background: #f1f5fb;
  font-weight: bold;
  font-size: 13px;
}
.schedule-table td.空闲 {
  background-color: #e8f5e9;
  color: #388e3c;
}
.schedule-table td.工作中 {
  background-color: #ffebee;
  color: #d32f2f;
  font-weight: bold;
}
.schedule-table td.已预约 {
  background-color: #fff3e0;
  color: #f57c00;
  font-weight: bold;
}
.schedule-table td.返回中 {
  background: #fffde7;
  color: #fbc02d;
}
.schedule-table td.待发货 {
  background: #f3e5f5;
  color: #8e24aa;
}
.schedule-table td.逾期 {
  background: #ffebee;
  color: #d32f2f;
}
.schedule-table td.已发货 {
  background: #e0f7fa;
  color: #00838f;
}
.schedule-table td.转寄 {
  background: #fbe9e7;
  color: #d84315;
}
.schedule-table td.租用中,
.schedule-table td.待发货,
.schedule-table td.已发货,
.schedule-table td.返回中,
.schedule-table td.逾期,
.schedule-table td.转寄 {
  background-color: #e3f2fd;
  color: #1565c0;
}
.schedule-table td:hover {
  background-color: rgba(25, 118, 210, 0.15);
  box-shadow: inset 0 0 0 1px #1976d2;
  z-index: 2;
  position: relative;
}
.schedule-status-legend {
  margin-top: 12px;
  display: flex;
  gap: 18px;
  flex-wrap: wrap;
}
.schedule-status-legend span {
  display: inline-block;
  min-width: 60px;
  padding: 4px 12px;
  border-radius: 4px;
  font-size: 0.9rem;
  text-align: center;
  margin-right: 6px;
  border: none;
  font-weight: normal;
  letter-spacing: 0;
  box-sizing: border-box;
}
.schedule-status-legend .空闲 { background: #e8f5e9; color: #388e3c; border-color: #c8e6c9; }
.schedule-status-legend .租用中 { background: #e3f2fd; color: #1976d2; border-color: #90caf9; }
.schedule-status-legend .返回中 { background: #fffde7; color: #fbc02d; border-color: #ffe082; }
.schedule-status-legend .待发货 { background: #f3e5f5; color: #8e24aa; border-color: #ce93d8; }
.schedule-status-legend .逾期 { background: #ffebee; color: #d32f2f; border-color: #ef9a9a; }
.schedule-status-legend .已发货 { background: #e0f7fa; color: #00838f; border-color: #80deea; }
.schedule-status-legend .转寄 { background: #fbe9e7; color: #d84315; border-color: #ffab91; }
.schedule-table-wrap {
  overflow-x: auto;
  background: #fafbfc;
  border-radius: 8px;
}
.schedule-table-small {
  font-size: 12px;
  font-family: 'Segoe UI', 'Arial', 'Microsoft YaHei', sans-serif;
  font-weight: 500;
  letter-spacing: 0.5px;
}
.schedule-table-small th, .schedule-table-small td {
  min-width: 100px;
  max-width: 120px;
  width: 110px;
  text-align: center;
  white-space: nowrap;
  font-size: 13px;
  font-family: 'Segoe UI', 'Arial', 'Microsoft YaHei', sans-serif;
  font-weight: 500;
  letter-spacing: 0.5px;
}
.status-tag {
  display: inline;
  min-width: 0;
  padding: 0;
  border-radius: 0;
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  background: none;
  color: #222;
  border: none;
  box-sizing: border-box;
}
.status-tag-空闲, .status-tag-租用中, .status-tag-返回中, .status-tag-待发货, .status-tag-逾期, .status-tag-已发货, .status-tag-转寄 {
  background: none;
  color: #222;
  border: none;
}
.schedule-toolbar-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 18px;
}
.schedule-title {
  color: #2196f3;
  font-weight: bold;
  font-size: 1.15rem;
  margin-right: 18px;
  letter-spacing: 1px;
}
.schedule-date-input {
  width: 130px;
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #bdbdbd;
  font-size: 1rem;
  margin-right: 6px;
}
.status-select {
  width: 70px;
  height: 32px;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  font-size: 15px;
  font-weight: 500;
  text-align: center;
  background: #fff;
  outline: none;
  padding: 0 8px;
  margin: 0 auto;
  display: block;
  cursor: pointer;
}
.status-select.status-tag-空闲 { background: #e8f5e9; color: #388e3c; border-color: #c8e6c9; }
.status-select.status-tag-租用中 { background: #e3f2fd; color: #1976d2; border-color: #90caf9; }
.status-select.status-tag-返回中 { background: #fffde7; color: #fbc02d; border-color: #ffe082; }
.status-select.status-tag-待发货 { background: #f3e5f5; color: #8e24aa; border-color: #ce93d8; }
.status-select.status-tag-逾期 { background: #ffebee; color: #d32f2f; border-color: #ef9a9a; }
.status-select.status-tag-已发货 { background: #e0f7fa; color: #00838f; border-color: #80deea; }
.status-select.status-tag-转寄 { background: #fbe9e7; color: #d84315; border-color: #ffab91; } 
.schedule-table td[title]:hover {
  cursor: help;
}